import { configureStore, createSlice } from '@reduxjs/toolkit'

const StageEnum = {
  break: 'break',
  session: 'session',
}

const defaultConfig = (() => {
  const sessionLength = 25
  return {
    sessionLength,
    breakLength: 5,
    isPause: true,
    currStage: StageEnum.session,
    leftLength: sessionLength * 60,
    timer: null,
    isTimeUp: false,
  }
})()

const USER_CONFIG_KEY = 'USER_CONFIG_KEY'

const getUserConfigFromLocalStorage = () => {
  return JSON.parse(localStorage.getItem(USER_CONFIG_KEY))
}

const initialState = (() => {
  const userConfig = getUserConfigFromLocalStorage()
  return userConfig ? userConfig : defaultConfig
})()

function changeLeftLengthBy_Com(state, delta) {
  state.leftLength += delta
  if (state.leftLength < 0) state.leftLength = 0
}

const slice = createSlice({
  name: '25+5Clock',
  initialState,
  reducers: {
    changeLeftLengthBy: (state, action) => {
      changeLeftLengthBy_Com(state, action.payload)
    },
    setSessionLength: (state, action) => {
      const MIN_LEN = 1
      const MAX_LEN = 60
      if (
        !state.isPause ||
        action.payload < MIN_LEN ||
        action.payload > MAX_LEN
      )
        return
      const delta = action.payload - state.sessionLength
      state.sessionLength = action.payload
      if (state.currStage === StageEnum.session)
        state.leftLength=action.payload*60
    },
    setBreakLength: (state, action) => {
      const MIN_LEN = 1
      const MAX_LEN = 60
      if (
        !state.isPause ||
        action.payload < MIN_LEN ||
        action.payload > MAX_LEN
      )
        return
      const delta = action.payload - state.breakLength
      state.breakLength = action.payload
      if (state.currStage === StageEnum.break)
        state.leftLength=action.payload*60
    },
    setIsTimeUp: (state, action) => {
      state.isTimeUp = action.payload
    },
    changeToNextStage: state => {
      state.currStage =
        state.currStage === StageEnum.session
          ? StageEnum.break
          : StageEnum.session
      state.leftLength =
        state.currStage === StageEnum.session
          ? state.sessionLength*60
          : state.breakLength*60
    },

    start: (state, action) => {
      state.isPause = false
    },
    pause: (state, action) => {
      state.isPause = true
    },
    reset: () => defaultConfig,
  },
})
export const store = configureStore({
  reducer: slice.reducer,
})

export const actions = slice.actions
