import { useEffect, useRef } from 'react'
import tw, { css } from 'twin.macro'
import RestoreIcon from '@mui/icons-material/Restore'
import { useDispatch, useSelector } from 'react-redux'
import { actions } from './store/store-slice.js'
import WithUpDownArrow from './component/WithUpDownArrow.jsx'
import { StartPauseBtn } from './component/StartPauseBtn.jsx'

export function Calculator({ ...props }) {
  const isPause = useSelector(state => state.isPause)
  const breakLength = useSelector(state => state.breakLength)
  const sessionLength = useSelector(state => state.sessionLength)
  const leftLength = useSelector(state => state.leftLength)
  const currStage = useSelector(state => state.currStage)
  const dispatch = useDispatch()
  const handleStart = () => dispatch(actions.start())
  const handlePause = () => dispatch(actions.pause())
  const handleReset = () => {
    audioRef.current.pause()
    audioRef.current.currentTime=0
    dispatch(actions.reset())
  }
  const formattedTime =
    `${Math.floor(leftLength / 60)}`.padStart(2, '0') +
    ':' +
    `${leftLength % 60}`.padStart(2, '0')
  const isInTimeUpEdge = leftLength < 60
  useEffect(() => {
    if (isPause) {
      audioRef.current.pause()
      return
    }
    const timerId = setTimeout(() => {
      if (leftLength === 0) {
        audioRef.current.play()
        dispatch(actions.changeToNextStage())
        setTimeout(() => {
        }, 3000)
        return
      }
      dispatch(actions.changeLeftLengthBy(-1))
    }, 1000)
    return () => clearInterval(timerId)
  }, [isPause, dispatch, leftLength])
  const audioRef = useRef(null)
  return (
    <div css={[tw`flex flex-col gap-2 text-gray-100`]}>
      <audio id={'beep'} src={'src/assets/BeepSound.wav'} preload={'auto'} ref={audioRef} />
      <h1 css={[tw`text-center text-4xl`]}>25 + 5 Clock</h1>
      <label id={'break-label'} css={[tw`flex flex-col items-center text-2xl`]}>
        Break Length
        <WithUpDownArrow
          downProps={{ id: 'break-decrement' }}
          upProps={{ id: 'break-increment' }}
          changeValueBy={delta =>
            dispatch(actions.setBreakLength(breakLength + delta))
          }
        >
          <span id={'break-length'} css={[tw`inline-block min-w-8 text-center`]}>
            {breakLength}
          </span>
        </WithUpDownArrow>
      </label>{' '}
      <label
        id={'session-label'}
        css={[tw`flex flex-col items-center text-2xl`]}
      >
        Session Length
        <WithUpDownArrow
          downProps={{ id: 'session-decrement' }}
          upProps={{ id: 'session-increment' }}
          changeValueBy={delta =>
            dispatch(actions.setSessionLength(sessionLength + delta))
          }
        >
          <span id={'session-length'} css={[tw`inline-block min-w-8 text-center`]}>
            {sessionLength}
          </span>
        </WithUpDownArrow>
      </label>
      <label
        id={'timer-label'}
        css={[
          tw`flex flex-col items-center gap-2 rounded-3xl border-4 p-4 text-2xl [text-transform:capitalize]`,
          !isPause &&
            isInTimeUpEdge &&
            tw`border-red-400 text-red-400 transition`,
        ]}
      >
        {currStage}
        <p
          id={'time-left'}
          css={[
            tw`text-[2.5rem]`,
            !isPause &&
              isInTimeUpEdge &&
              css`
                animation: blink 1s step-start infinite;
                @keyframes blink {
                  0%,
                  100% {
                    opacity: 1;
                  }
                  50% {
                    opacity: 0;
                  }
                }
              `,
          ]}
        >
          {formattedTime}
        </p>
      </label>
      <div css={[tw`flex justify-center`]}>
        <StartPauseBtn
          id={'start_stop'}
          iconCss={[tw`text-4xl`]}
          isPause={isPause}
          setIsPause={isPaused => {
            if (isPaused) {
              handlePause()
            } else {
              handleStart()
            }
          }}
        />
        <button onClick={handleReset} id={'reset'}>
          <RestoreIcon css={[tw`text-4xl`]} />
        </button>
      </div>
    </div>
  )
}
