import PauseCircleOutlinedIcon from '@mui/icons-material/PauseCircleOutlined'
import PlayCircleFilledWhiteOutlinedIcon from '@mui/icons-material/PlayCircleFilledWhiteOutlined'
export function StartPauseBtn({ isPause, setIsPause,iconCss, ...props }) {
  return (
    <button
      onClick={() => {
        setIsPause(!isPause)
      }}
      {...props}
    >
      {isPause ? (
        <PlayCircleFilledWhiteOutlinedIcon css={iconCss}/>
      ) : (
        <PauseCircleOutlinedIcon css={iconCss}/>
      )}
    </button>
  )
}
