import ArrowDropDownCircleOutlinedIcon from '@mui/icons-material/ArrowDropDownCircleOutlined'
import tw from 'twin.macro'

export default function WithUpDownArrow({upProps,downProps, changeValueBy,children, ...props }) {
  return (
    <div {...props}>
      <button onClick={()=>changeValueBy(-1)} {...downProps}>
        <ArrowDropDownCircleOutlinedIcon   />
      </button>

      {children}
      <button onClick={()=>changeValueBy(1)} {...upProps}>
        <ArrowDropDownCircleOutlinedIcon  css={[tw`rotate-180`]}/>
      </button>
    </div>
  )
}
